.App {
  font-family: sans-serif;
}
h3{
  width: 100%;
  margin: 8px;
  text-align: center;
}
.pageGrid{
  display: grid;
  width: 800px;
  height: 600px;
  border: solid 1px gold;
  grid-template-columns: 2fr 4fr;
}
.pageMiddle{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.leftPane{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  border: solid 2px rgb(204, 198, 198);
}
.rightPane{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: solid 2px rgb(194, 202, 201);
}
.gradient{
  margin: 5px;
  width: 300px;
  height: 300px;
  border: solid 3px red;
  background:  radial-gradient(circle at 50% 50%,red 25px,blue 25px);
}
.contolPanel{
  margin: 5px;
}
.screenMain{
  display:grid;
  grid-template-rows: 3fr 1fr;
  grid-template-columns: 1fr;
  justify-content: space-evenly;
  align-items: center;
}
.CodeCSS{
  margin: 20px;
}